import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { SubjectService } from '../../services/subject.service';

@Component({
  standalone: true,
  selector: 'app-subject-list',
  imports: [CommonModule, RouterModule],
  templateUrl: './subject-list.component.html'
})
export class SubjectListComponent implements OnInit {
  subjects: any[] = [];
  deleteMessage: string | null = null; // Mensaje para la eliminaci贸n

  constructor(private subjectService: SubjectService) {}

  ngOnInit() {
    this.loadSubjects();
  }

  loadSubjects(): void {
    this.subjectService.getSubjectsWithUsers().subscribe(data => {
      this.subjects = data;
    });
  }

  deleteSubject(id: string): void {
    const confirmDelete = window.confirm('驴Est谩s seguro de que deseas eliminar esta materia?');
    if (!confirmDelete) return;

    this.subjectService.deleteSubject(id).subscribe({
      next: () => {
        // Actualizar el listado despu茅s de la eliminaci贸n
        this.subjects = this.subjects.filter(subject => subject._id !== id);
        this.deleteMessage = 'La materia ha sido eliminada correctamente.'; // Mensaje de 茅xito

        // Reiniciar el mensaje despu茅s de 3 segundos
        setTimeout(() => {
          this.deleteMessage = null;
        }, 3000);
      },
      error: err => {
        console.error('Error al eliminar materia:', err);
        alert('Ocurri贸 un error al eliminar la materia.');
      }
    });
  }
}
